import { useEffect, useState } from "react";

interface AnimatedLoadingTextProps {
  messages: string[];
  /** 每条消息显示的毫秒数，默认 2000 */
  interval?: number;
  className?: string;
}

/**
 * Cycles through a list of messages with a smooth fade transition,
 * giving users the feeling that the AI is actively working.
 */
const AnimatedLoadingText = ({ messages, interval = 2000, className = "" }: AnimatedLoadingTextProps) => {
  const [index, setIndex] = useState(0);
  const [visible, setVisible] = useState(true);

  useEffect(() => {
    if (messages.length <= 1) return;

    const cycle = setInterval(() => {
      // Fade out
      setVisible(false);
      setTimeout(() => {
        setIndex((prev) => (prev + 1) % messages.length);
        // Fade in
        setVisible(true);
      }, 300);
    }, interval);

    return () => clearInterval(cycle);
  }, [messages, interval]);

  return (
    <p
      className={`text-sm text-primary transition-opacity duration-300 ${visible ? "opacity-100" : "opacity-0"} ${className}`}
    >
      {messages[index]}
    </p>
  );
};

export default AnimatedLoadingText;
